  <div class="popup popup-add" id="save-ff" v-show="showStatus">
    <div class="pop-content">
      <p class="popup-title">
        添加主机联网设备
      </p>
      <div class="popup-con">
        <ul class="row">
          <li class="col-lg-6" :class="{'warning': $v.deviceType.$dirty && $v.deviceType.$invalid}">
            <p class="input-title">
              设备类型
              <small class="fr">*请选择设备类型</small>
            </p>
            <!-- <v-select :data-items="deviceTypes" placeholder="请选择设备类型" show-key="name" v-model="deviceType"></v-select> -->
          </li>
          <li class="col-lg-6" :class="{'warning': $v.info.brandNum.$dirty && $v.info.brandNum.$invalid}" v-if="isSType">
            <p class="input-title">
              厂商
              <small class="fr">*请选择厂商</small>
            </p>
            <!-- <v-select :data-items="brands" placeholder="请选择厂商" show-key="name" v-model="info.brandNum"></v-select> -->
          </li>
          <li class="col-lg-6" :class="{'warning': $v.info.brandNum.$dirty && $v.info.brandNum.$invalid}"  v-if="isSType">
            <p class="input-title">
              型号
              <small class="fr">*请选择型号</small>
            </p>
            <!-- <v-select :data-items="models" placeholder="请选择型号" show-key="name" v-model="info.modelNum"></v-select> -->
          </li>
          <li class=" col-lg-6" :class="{'warning': $v.info.userAccount.$dirty && $v.info.userAccount.$invalid}" v-if="isMType">
            <p class="input-title">
              设备账号
              <small class="fr">*请输入设备账号</small>
            </p>
            <input type="text" placeholder="请输入设备账号" v-model="info.userAccount" @input="$v.info.userAccount.$touch"/>
          </li>
          <li class=" col-lg-6" :class="{'warning': $v.info.password.$dirty && $v.info.password.$invalid}" v-if="isMType">
            <p class="input-title">
              设备密码
              <small class="fr">*请输入设备密码</small>
            </p>
            <input type="text" placeholder="请输入设备密码" v-model="info.password" @input="$v.info.password.$touch"/>
          </li>
          <li class="col-lg-6" :class="{'warning': $v.info.mac.$dirty && $v.info.mac.$invalid}">
            <p class="input-title">
              MAC
              <small class="fr">*请输入设备MAC</small>
            </p>
            <input type="text" placeholder="请输入设备设备MAC" v-model="info.mac" @input="$v.info.mac.$touch"/>
          </li>
          <li class="col-lg-6">
            <p class="input-title">
              楼栋
              <small class="fr">*请输入设备MAC/IMEI</small>
            </p>
            <input type="text" placeholder="请输入楼栋" v-model="info.building"/>
          </li>
          <li class="col-lg-6">
            <p class="input-title">
              楼层
              <small class="fr">*请输入楼层</small>
            </p>
            <input type="text" placeholder="请输入楼层" v-model="info.floor"/>
          </li>
          <li class="col-lg-6">
            <p class="input-title">
              房间号
              <small class="fr">*请输入房间号</small>
            </p>
            <input type="text" placeholder="请输入房间号" v-model="info.room"/>
          </li>
          <li class=" col-lg-6" :class="{'warning': $v.info.position.$dirty && $v.info.position.$invalid}">
            <p class="input-title">
              场所坐标
              <small class="fr">*请输入该设备所在场所坐标</small>
            </p>
            <input type="text" placeholder="请输入该设备所在场所坐标" v-model="info.position" @input="$v.info.position.$touch"/>
            <span class="tips">点击 <a href="http://api.map.baidu.com/lbsapi/getpoint/index.html" target="_blank">这里</a> 查看如何获取坐标</span>
          </li>
          <li class="col-lg-12" :class="{'warning': $v.supervisors.$dirty && $v.supervisors.$invalid}">
            <p class="input-title">
              监管人
              <small class="fr">*请选择设备监管人</small>
            </p>
            <div class="multiselect">
              <input type="text" readonly="readonly" placeholder="请选择设备监管人，可多选" v-model="supervisorsName" @click="showSuperVisorList($event)"/>
              <i class="xsj"></i>
              <div class="dropdown">
                <ul>
                  <li v-for="item in allSupervisors" :key="item.id" @click="chooseUser(item)" :class="{checked: item.flag}">
                    {{item.name}}
                  </li>
                </ul>
              </div>
            </div>
          </li>
          <li class="cl text-center">
            <button class="btn btn-middle cancel" @click="showStatus=!showStatus">取消</button>
            <button class="btn btn-middle yes" @click="submit">添加</button>
          </li>
        </ul>
      </div>
      <!--关闭按钮-->
      <span class="layui-layer-setwin">
			<a class="close-popup layui-layer-close layui-layer-close1" href="javascript:;" @click="showStatus=!showStatus">
        <!-- <img src="../../../assets/img/close.png" title="关闭"/> -->
			</a>
		</span>
    </div>
  </div>
</template>
<script>
// import vSelect from '../../base/input/v_select'
import { withParams } from 'vuelidate/lib/validators/common'
import { required } from 'vuelidate/lib/validators'
import msgUtil from '@/utils/msgUtil'
import { getFFhostBrands } from '@/api/device'
// import deviceApi from '../../../server_api/device_info'

export default {
  data() {
    return {
      showStatus: false,
      brands: [],
      models: [],
      deviceType: undefined,
      isSType: false,
      isMType: false,
      deviceTypes: [
        { name: 'S型', code: 0 }, { name: 'M型', code: 1 }
      ],
      info: {
        mac: '',
        userAccount: '',
        password: '',
        building: '',
        floor: '',
        room: '',
        brandNum: '',
        brandName: '',
        modelNum: '',
        position: ''
      },
      supervisorsName: '',
      supervisors: []
    }
  },
  validations: {
    info: {
      mac: {
        required,
        format: withParams({ type: 'format' }, val => {
          return val && /^[a-zA-Z0-9]{12}$/.test(val)
        })
      },
      userAccount: {
        required
      },
      password: {
        required
      },
      brandNum: {
        required
      },
      modelNum: {
        required
      },
      position: {
        required,
        format: withParams({ type: 'format' }, val => {
          const strs = val.split(',')
          if (strs.length !== 2) {
            return false
          } else {
            const lng = +strs[0]
            const lat = +strs[1]
            return lng >= -180 && lng <= 180 && lat >= -90 && lat <= 90
          }
        })
      }
    },
    deviceType: {
      required
    },
    supervisors: {
      required
    }
  },
  computed: {
    placeItem() {
      return this.$store.state.place.checkItem
    },
    allSupervisors() {
      console.log(this.placeItem)
      return this.placeItem.supervisors.map(s => {
        return {
          name: s.name,
          duty: s.duty,
          id: s.id,
          phone: s.phone,
          flag: false
        }
      })
    }
  },
  watch: {
    'info.brandNum': function(val) {
      if (val && val.models) {
        this.models = val.models
        this.info.modelNum = ''
      }
    },
    deviceType(val) {
      if (val) {
        this.isMType = val.code === 1
        this.isSType = !this.isMType
        if (this.isMType) {
          this.info.userAccount = ''
          this.info.password = ''
          this.info.brandNum = { code: 0 }
          this.info.modelNum = { code: 0 }
        } else {
          this.info.userAccount = 'default'
          this.info.password = 'default'
          this.info.brandNum = ''
          this.info.modelNum = ''
        }
      }
    }
  },
  methods: {
    reset() {
      this.supervisorsName = ''
      this.supervisors = []
      this.brands = this.brands.slice()
      this.models = []
      this.allSupervisors.forEach(a => {
        a.flag = false
      })
      this.info = {
        mac: '',
        userAccount: '',
        password: '',
        building: '',
        floor: '',
        room: '',
        brandNum: '',
        brandName: '',
        modelNum: '',
        position: ''
      }
      this.deviceTypes = this.deviceTypes.slice()
      this.deviceType = undefined
      this.$v.info.mac.$reset()
      this.$v.info.userAccount.$reset()
      this.$v.info.password.$reset()
      this.$v.info.brandNum.$reset()
      this.$v.info.modelNum.$reset()
      this.$v.info.position.$reset()
      this.$v.deviceType.$reset()
      this.$v.supervisors.$reset()
    },
    show() {
      this.reset()
      this.showStatus = true
      this.$el.style.zIndex = Date.now() % 100000000
    },
    close() {
      this.showStatus = false
    },
    showSuperVisorList(e) {
      // $(e.target).siblings('.dropdown').slideToggle()
    },
    queryBrands() {
      getFFhostBrands().then(resp => {
        const data = Object.keys(resp.data).map(bk => {
          return {
            name: resp.data[bk].name,
            code: +bk,
            models: resp.data[bk].models.map(md => {
              return Object.keys(md).map(mdk => {
                return {
                  code: +mdk,
                  name: md[mdk]
                }
              })[0]
            })
          }
        })
        this.brands = data
      }).catch(e => {
        msgUtil.error('加载厂商数据失败', msgUtil.findErrorMsg(e))
      })
    },
    chooseUser(item) {
      item.flag = !item.flag
      this.supervisors = this.allSupervisors.filter(p => p.flag)
      this.supervisorsName = this.supervisors.map(p => p.name).join(',')
    },
    validateAll() {
      this.$v.info.mac.$touch()
      this.$v.info.userAccount.$touch()
      this.$v.info.password.$touch()
      this.$v.info.brandNum.$touch()
      this.$v.info.modelNum.$touch()
      this.$v.info.position.$touch()
      this.$v.deviceType.$touch()
      this.$v.supervisors.$touch()
    },
    submit() {
      this.validateAll()
      if (!this.$v.info.$invalid && !this.$v.supervisors.$invalid) {
        const supervisors = this.supervisors.map(s => s.phone)
        const lngLat = this.info.position.split(',')
        const postData = {
          building: this.info.building || '',
          room: this.info.room || '',
          floor: this.info.floor || '',
          pid: this.placeItem.place.id,
          type: 1,
          number: this.info.mac,
          phone: supervisors[0],
          supervisors: supervisors,
          lat: +lngLat[1],
          lng: +lngLat[0],
          types: 24,
          ffhost: {
            brand_num: +this.info.brandNum.code,
            model_num: +this.info.modelNum.code,
            uid: this.info.userAccount === 'default' ? undefined : this.info.userAccount,
            password: this.info.password === 'default' ? undefined : this.info.password
          }
        }
        this.postData(postData)
      }
    },
    postData(data) {
      return deviceApi.bindDevice(data).then(resp => {
        msgUtil.success('保存成功')
        this.showStatus = false
        this.$parent.queryDevices()
      }, error => {
        msgUtil.error('保存失败', msgUtil.findErrorMsg(error))
      })
    }
  },
  created() {
    this.queryBrands()
  },
  components: {
    // vSelect
  }
}
</script>
