<template>
  <div class="device-smoke">
    <p class="tools"><span class="add-video" @click="handleAddVideo">添加视频</span></p>
    <div class="data">
      <iot-table
        :tableHeader="tableHeader"
        :tableData="tableData"
        :pagination="pagination"
        :loading="loading"
        @reloaddata="initData"
      >
        <div slot="col-install_address" slot-scope="scope"><p>{{scope.row.install_address}}-{{scope.row.building}}-{{scope.row.floor}}-{{scope.row.room}}</p></div>
        <div slot="col-confirm" slot-scope="scope">
          <el-button @click="handleShowVideo(scope.row)" type="text" size="small"><span class="iconfont iconshipin"></span></el-button>
          <el-button @click="handleUpdateVideo(scope.row)" type="text" size="small"><span class="iconfont iconbianji"></span></el-button>
          <el-button @click="handleDelete(scope.row)" type="text" size="small"><span class="iconfont iconshanchu"></span></el-button>
        </div>
      </iot-table>
    </div>
  </div>
</template>

<script>
import { placeVideos, delVideo, getVideoUrl } from '@/api/device'
import msgUtil from '@/utils/msgUtil'
import $ from 'jquery'
import iotTable from '@/components/iotTable'

export default {
  name: 'DeviceSmoke',
  components: {
    iotTable
  },
  data() {
    return {
      loading: false,
      queryFilter: {
        mac: '',
        address: ''
      },
      tableData: [],
      pagination: {
        page: 1,
        per_page: 10,
        total: 0
      },
      tableHeader: [
        { prop: 'install_address', label: '安装位置', type: 'slot' },
        { prop: 'mac', label: '设备mac' },
        { prop: 'url', label: '视频流地址' },
        { prop: 'confirm', label: '操作', type: 'slot' }
      ]
    }
  },
  computed: {
    activeSite() {
      return this.$store.state.place.checkItem
    }
  },
  mounted() {
    if (this.activeSite.place.id) {
      this.initData()
    }
  },
  methods: {
    async initData() {
      this.loading = true
      const params = {
        page: this.pagination.page,
        per_page: this.pagination.per_page,
        type: 23, // video 此处是23
        mac: this.queryFilter.mac ? this.queryFilter.mac : undefined,
        address: this.queryFilter.address ? this.queryFilter.address : undefined
      }
      try {
        const { data } = await placeVideos(this.$store.state.place.checkItem.place.id || this.$route.query.placeid, params)
        if (data.page_info.total > 0) {
          data.items.forEach(i => {
            // console.log(i)
            i.install_address = this.activeSite.place.name
          })
          this.noDevices = false
        } else {
          this.noDevices = true
        }
        this.tableData = data.items
        this.pagination = {
          total: data.page_info.total,
          page: data.page_info.page,
          per_page: data.page_info.per_page
        }
      } catch (error) {
        msgUtil.error('加载项目数据失败', msgUtil.findErrorMsg(error))
      }
      this.loading = false
    },
    handleAddVideo() {
      this.$router.push({
        path: '/equipment/video/add'
      })
    },
    handleUpdateVideo(row) {
      this.$router.push({
        path: '/equipment/video/edit',
        query: {
          mac: row.mac,
          id: row.id,
          address: row.address,
          url: row.url
        }
      })
      // this.$refs.mapMark.show(row.lng, row.lat, row)
    },
    handleDelete(row) {
      msgUtil.confirm('确定要删除？', '删除后设备无法查看视频!', () => {
        delVideo(row.id).then(resp => {
          if (resp.status === 200) {
            msgUtil.info('删除成功')
            this.initData()
          } else {
            msgUtil.error('删除失败', resp.msg)
          }
        }, error => {
          msgUtil.error('删除失败', msgUtil.findErrorMsg(error))
        })
      })
    },
    handleShowVideo(row) {
      var devId; var chnl; var streamType;
      var rowUrlArr = row.url.split('/');
      devId = rowUrlArr[0];
      chnl = rowUrlArr[1] ? row.url.split('/')[1] * 1 - 1 : 0;
      streamType = (rowUrlArr[2] && rowUrlArr[2] != 'main') ? 1 : 0;
      // const loading = layer.load(1, { shade: [0.6, '#393D49'] })
      var paramobj = {
        devId: devId,
        chnl: chnl + '',
        streamType: streamType + ''
      };

      getVideoUrl(paramobj).then((res) => {
        const code = res.code;
        const errorOBJ = {
          '-4401': '盒子不通',
          '-4402': '盒子下的设备不通',
          '-4403': '登录盒子下的设备的用户名密码不匹配',
          '-4404': '监听设备连接超时',
          '-4405': '接收设备数据超时',
          '-4406': '错误的设备数据格式',
          '-4407': '此通道不存在'
        };
        // layer.close(loading)
        if (code == 0) {
          const videoUrl = res.datas.hlsUrl;
          const vlc = document.getElementById('vlc_player');
          $('#videoFrameBox').show();
          if (vlc.playlist) {
            // 用VLC播放，只有IE8以上支持
            vlc.playlist.items.clear();
            document.getElementById('vlc_player_container').style.top = '0';
            const itemId = vlc.playlist.add(videoUrl);
            vlc.playlist.playItem(itemId);
          } else {
            // 其他浏览器用video播放
            msgUtil.error('未检测到vlc插件，正在切换网络播放模式')
            var $videoFrame = $('#videoFrame');
            $videoFrame.show();
            if ($('#myVideo').length == 0) {
              $('#videoFrame').append("<video id='myVideo' class='video-js vjs-default-skin  vjs-big-play-centered' controls preload='none' data-setup='{}' width='1200px' height='500px' ></video>");
            }
            var myPlayer = videojs('myVideo');
            myPlayer.src({
              src: videoUrl,
              type: 'application/x-mpegURL'
            });
            myPlayer.load();
            myPlayer.play();
            window.onbeforeunload = function() {
              if (myPlayer != null) myPlayer.dispose();
            };
          }
        } else {
          const err = [-3301, -3302, -3303, -3304, -3305, -3306, -3307, -3308, -3309, -3310, -3311, -3312, -3313];
          const isHave = err.includes(code);
          if (isHave) {
            msgUtil.error(code + '：服务器内部错误');
            return;
          }
          if (errorOBJ[code]) {
            msgUtil.error(code + '：' + errorOBJ[code]);
            return;
          } else {
            msgUtil.error(code + '：未知错误');
          }
        }
      });
    }
  }
}
</script>

<style lang="less" scoped>
.tools {
  margin: 0 0 20px 0;
  text-align: right;
}
.add-video {
  display: block;
  color: #409EFF;
  font-size: 14px;
  cursor: pointer;
}
</style>
