<template>
  <div class="deviceFireEngine">
    <div class="lists">
      <!-- <div class="add-device device-item" @click="handleShowadd"><span class="iconfont iconjia"></span></div> -->
      <device-fire-engine-card class="device-item" v-for="(item, i) in devices" :key="i" :ff-device="item"></device-fire-engine-card>
    </div>
    <save-device ref="saveDevice"></save-device>
  </div>
</template>

<script>
import deviceFireEngineCard from './deviceFireEngineCard'
import { placeFFhosts } from '@/api/device'
import msgUtil from '@/utils/msgUtil'
import saveDevice from './dialogs/add_fire_engine'


export default {
  name: 'DeviceFireEngine',
  components: {
    deviceFireEngineCard,
    saveDevice
  },
  mounted() {
    this.queryDevices()
  },
  data() {
    return {
      devices: []
    }
  },
  computed: {
    activeSite() {
      return this.$store.state.place.checkItem
    }
  },
  watch: {
    'activeSite.place.id': function(val) {
      if (val !== undefined) {
        this.queryDevices()
      }
    }
  },
  methods: {
    async queryDevices() {
      try {
        const { data } = await placeFFhosts(this.activeSite.place.id)
        this.devices = data
      } catch (error) {
        msgUtil.error('加载设备失败', msgUtil.findErrorMsg(error))
      }
    },
    handleShowadd() {
      this.$refs.saveDevice.show()
    }
  }
}
</script>

<style lang="less" scoped>
  .lists {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    .device-item {
      width: 23.5%;
      margin-right: 2%;
      margin-bottom: 20px;
      box-sizing: border-box;
      &:nth-child(4n) {
        margin-right: 0;
      }
    }
  }
  .add-device {
    border: 1px dashed #15a2eb;
  }

</style>
