<template>
  <div class="popup yh-detail map_mark">
    <div :id="mapId"></div>
    <span class="layui-layer-setwin" id="btn-close">
				<a class="close-popup layui-layer-close layui-layer-close1" href="javascript:;" @click="cancel">
          <span class="iconfont iconguanbi closeicon" ></span>
				</a>
			</span>
  </div>
</template>
<script>
import msgUtil from '@/utils/msgUtil'

export default {
  data() {
    return {
      mapId: `map_${Date.now()}`,
      map: undefined,
      position: {
        lng: 116,
        lat: 40
      },
      position_new: {
        lng: 116,
        lat: 40
      },
      obj: undefined
    }
  },
  methods: {
    show(lng, lat, obj) {
      this.obj = obj
      this.map.clearOverlays()
      this.$el.style.top = '0px'
      this.position.lat = lat - 0
      this.position.lng = lng - 0
      this.position_new.lng = this.position.lng
      this.position_new.lat = this.position.lat
      this.showStatus = true
      const point = new BMap.Point(this.position.lng, this.position.lat)
      this.map.centerAndZoom(new BMap.Point(this.position.lng, this.position.lat), 19)
      this.map.enableScrollWheelZoom(true)
      const icon = new BMap.Icon('./static/img/icon1.png', new BMap.Size(25, 25))
      const marker = new BMap.Marker(point, { icon: icon })
      marker.enableDragging()
      marker.addEventListener('dragend', (e) => {
        this.position_new.lng = e.point.lng
        this.position_new.lat = e.point.lat
      })
      this.map.addOverlay(marker)
    },
    cancel() {
      if (this.obj && (this.position.lat !== this.position_new.lat || this.position.lng !== this.position_new.lng)) {
        msgUtil.confirm('定位发生改变', '是否保存更新定位信息', () => {
          this.$emit('point-change', {
            obj: this.obj,
            lng: this.position_new.lng,
            lat: this.position_new.lat
          })
        })
      }
      this.$el.style.top = '-99999px'
    }
  },
  mounted() {
    this.map = new BMap.Map(document.getElementById(this.mapId), { enableMapClick: true, mapType: BMAP_HYBRID_MAP })
    this.map.addControl(new BMap.MapTypeControl(
      {
        mapTypes: [
          BMAP_NORMAL_MAP,
          BMAP_HYBRID_MAP
        ] }
    ))
  }
}
</script>
