 -->
<template>
  <div class="device-smoke">
    <div class="data">
      <iot-table
        :tableHeader="tableHeader"
        :tableData="tableData"
        :pagination="pagination"
        :loading="loading"
        @reloaddata="initData"
      >
        <div slot="col-province" slot-scope="scope"><p>{{scope.row.province}}/{{scope.row.city}}/{{scope.row.county}}</p></div>
        <div slot="col-name" slot-scope="scope"><p>{{scope.row.name}}  {{scope.row.phone}}</p></div>
        <div slot="col-create_time" slot-scope="scope"><p>{{scope.row.create_time ? calcTime(scope.row.create_time) : ''}}</p></div>
        <div slot="col-confirm" slot-scope="scope">
          <el-button @click="handleMap(scope.row)" type="text" size="small"><span class="iconfont iconditu"></span></el-button>
          <el-button @click="handleDelete(scope.row)" type="text" size="small"><span class="iconfont iconshanchu"></span></el-button>
        </div>
      </iot-table>
    </div>
    <map-mark ref="mapMark" v-on:point-change="saveLocation"></map-mark>
  </div>
</template>

<script>
import { unbindDevicenew, placePatrolHistory } from '@/api/device'
import msgUtil from '@/utils/msgUtil'
import mapMark from './dialogs/map_mark'
import dateUtil from '@/utils/dateUtil'
import iotTable from '@/components/iotTable'

export default {
  name: 'DeviceSmoke',
  components: {
    mapMark,
    iotTable
  },
  data() {
    return {
      loading: true,
      queryFilter: {
        start: '',
        end: ''
      },
      tableData: [],
      pagination: {
        page: 1,
        per_page: 10,
        total: 0
      },
      tableHeader: [
        { prop: 'province', label: '省/市、区', type: 'slot' },
        { prop: 'address', label: '巡检地址' },
        { prop: 'name', label: '巡检人' },
        { prop: 'mac', label: '设备mac' },
        { prop: 'create_time', label: '巡检时间', type: 'slot' },
        { prop: 'confirm', label: '操作', type: 'slot' }
      ]
    }
  },
  computed: {
    activeSite() {
      return this.$store.state.place.checkItem
    },
    calcTime() {
      return (time) => {
        return dateUtil.format(new Date(time * 1000), 'YYYY-MM-dd HH:mm:ss')
      }
    }
  },
  mounted() {
    if (this.activeSite.place.id) {
      this.initData()
    }
  },
  methods: {
    async initData() {
      this.loading = true
      const params = {
        page: this.pagination.page,
        per_page: this.pagination.per_page,
        start: Math.ceil(this.queryFilter.start / 1000),
        end: Math.ceil(this.queryFilter.end / 1000),
        pid: this.activeSite.place.id
      }
      try {
        const { data } = await placePatrolHistory(this.$store.state.place.checkItem.place.id || this.$route.query.placeid, params)
        if (data.page_info.total > 0) {
          data.items.forEach(i => {
            // console.log(i)
            i.install_address = this.activeSite.place.name
          })
          this.noDevices = false
        } else {
          this.noDevices = true
        }
        this.tableData = data.items
        this.pagination = {
          total: data.page_info.total,
          page: data.page_info.page,
          per_page: data.page_info.per_page
        }
      } catch (error) {
        msgUtil.error('加载项目数据失败', msgUtil.findErrorMsg(error))
      }
      this.loading = false
    },
    handleMap(row) {
      this.$refs.mapMark.show(row.lng, row.lat, row)
    },
    handleDelete(row) {
      msgUtil.confirmdevice('确定要解绑？', row.mac, '解绑后将无法接收设备消息!', (verifycode) => {
        unbindDevicenew(row.mac, verifycode).then(resp => {
          if (resp.status === 200) {
            msgUtil.info('解绑成功')
            this.initData()
          } else {
            msgUtil.error('解绑失败', resp.msg)
          }
        }, error => {
          msgUtil.error('解绑失败', msgUtil.findErrorMsg(error))
        })
      })
    },
    handleDetails(row) {
      this.$router.push({
        path: `/equipment/devices-list/${this.$route.params.type}/${row.mac}`
      })
    }
  }
}
</script>

<style lang="less" scoped>
  .device-smoke{

  }
</style>
