<template>
  <div class="deviceFireEngine">
    <div class="select-tab">
      <span class="iconfont iconfenlei" :class="{active: status === 1}" @click="setStatus(1)"></span>
      <span class="iconfont iconfenlei1" :class="{active: status === 2}" @click="setStatus(2)"></span>
      <span class="iconfont iconbianji" :class="{active: status === 3}" @click="setStatus(3)"></span>
    </div>
    <div class="con" v-if="status === 1">
      <iot-table
        :tableHeader="tableHeader"
        :tableData="tableData"
        :pagination="pagination"
        :loading="loading"
        @reloaddata="queryDeviceslist"
      >
        <div slot="col-province" slot-scope="scope"><p>{{scope.row.province}}{{scope.row.city}}{{scope.row.county}}{{scope.row.street}}</p></div>
        <div slot="col-electrovalency" slot-scope="scope"><p>{{scope.row.electrovalency}}元</p></div>
        <div slot="col-activepower_totle" slot-scope="scope"><p>{{scope.row.activepower_totle}}W</p></div>
        <div slot="col-energy_total" slot-scope="scope"><p>{{scope.row.energy_total}}KW·h</p></div>
      </iot-table>
    </div>
    <div class="con lists" v-if="status === 2">
      <device-elestric-card class="device-item" v-for="(item, i) in devices" :key="i" :device="item"></device-elestric-card>
      <div class="device-item add" @click="showAdd"><span class="iconfont iconjia"></span></div>
    </div>
    <div class="con" v-if="status === 3">
      <div class="select-type">
        <div class="sel">
          <div class="item" :class="{active: reporttype === 1}" @click="setreporttype(1)">
            <span class="cir"></span>
            <span class="text">项目统计表</span>
          </div>
          <div class="item" :class="{active: reporttype === 2}" @click="setreporttype(2)">
            <span class="cir"></span>
            <span class="text">电量统计表</span>
          </div>
        </div>
        <div class="btns">
          <el-button class="green" type="primary" @click="lookday">查看日报表</el-button>
          <el-button class="green" type="primary" @click="lookmonth">查看月报表</el-button>
          <el-button class="green" type="primary" @click="lookyear">查看年报表</el-button>
        </div>
      </div>
      <div class="table t1" v-if="reporttype === 1">
        <iot-table
          :tableHeader="tableHeader1"
          :tableData="tableData1"
          :pagination="pagination1"
          :loading="loading"
          :hidepagination="true"
          @reloaddata="getreportform"
        >
          <div slot="col-room" slot-scope="scope"><p v-if="scope.row.room">{{scope.row.room}}</p><p v-else>无</p></div>
          <div slot="col-mac" slot-scope="scope"><p v-if="scope.row.mac">{{scope.row.mac}}</p><p v-else>无</p></div>
          <div slot="col-energy_data_active" slot-scope="scope"><p v-if="scope.row.energy_data_active">{{scope.row.energy_data_active}}</p><p v-else>无</p></div>
          <div slot="col-energy_data_reactive" slot-scope="scope"><p v-if="scope.row.energy_data_reactive">{{scope.row.energy_data_reactive}}</p><p v-else>无</p></div>
          <div slot="col-max_electric_a" slot-scope="scope"><p v-if="scope.row.max_electric_a">{{scope.row.max_electric_a}}</p><p v-else>无</p></div>
          <div slot="col-min_electric_a" slot-scope="scope"><p v-if="scope.row.min_electric_a">{{scope.row.min_electric_a}}</p><p v-else>无</p></div>
          <div slot="col-average_electric_a" slot-scope="scope"><p v-if="scope.row.average_electric_a">{{scope.row.average_electric_a}}</p><p v-else>无</p></div>
          <div slot="col-max_electric_b" slot-scope="scope"><p v-if="scope.row.max_electric_b">{{scope.row.max_electric_b}}</p><p v-else>无</p></div>
          <div slot="col-min_electric_b" slot-scope="scope"><p v-if="scope.row.min_electric_b">{{scope.row.min_electric_b}}</p><p v-else>无</p></div>
          <div slot="col-average_electric_b" slot-scope="scope"><p v-if="scope.row.average_electric_b">{{scope.row.average_electric_b}}</p><p v-else>无</p></div>
          <div slot="col-max_electric_c" slot-scope="scope"><p v-if="scope.row.max_electric_c">{{scope.row.max_electric_c}}</p><p v-else>无</p></div>
          <div slot="col-min_electric_c" slot-scope="scope"><p v-if="scope.row.min_electric_c">{{scope.row.min_electric_c}}</p><p v-else>无</p></div>
          <div slot="col-average_electric_c" slot-scope="scope"><p v-if="scope.row.average_electric_c">{{scope.row.average_electric_c}}</p><p v-else>无</p></div>
          <div slot="col-first_time" slot-scope="scope"><p v-if="scope.row.first_time">{{scope.row.first_time}}</p><p v-else>无</p></div>
          <div slot="col-last_time" slot-scope="scope"><p v-if="scope.row.last_time">{{scope.row.last_time}}</p><p v-else>无</p></div>
        </iot-table>
      </div>
      <div class="table t2" v-if="reporttype === 2">
        <iot-table
          :tableHeader="tableHeader2"
          :tableData="tableData2"
          :pagination="pagination2"
          :loading="loading"
          :hidepagination="true"
          @reloaddata="getreportform"
        >
          <div slot="col-room" slot-scope="scope">
            <p v-if="scope.row.room">{{scope.row.room}}</p>
            <p v-else>无</p>
          </div>
          <div slot="col-energy_data" slot-scope="scope">
            <p v-if="scope.row.energy_data">{{scope.row.energy_data}}</p>
            <p v-else>无</p>
          </div>
          <div slot="col-first_time" slot-scope="scope">
            <p v-if="scope.row.first_time">{{scope.row.first_time}}</p>
            <p v-else>无</p>
          </div>
          <div slot="col-last_time" slot-scope="scope">
            <p v-if="scope.row.last_time">{{scope.row.last_time}}</p>
            <p v-else>无</p>
          </div>
        </iot-table>
      </div>
    </div>
    <save-device ref="saveDevice"></save-device>
  </div>
</template>

<script>
import iotTable from '@/components/iotTable'
import msgUtil from '@/utils/msgUtil'
import { placeelectricitylist, placeelectricity, getelectricityreportplace } from '@/api/device'
import deviceElestricCard from './deviceElestricCard'
import saveDevice from './dialogs/add_elestric_engine'


export default {
  name: 'DeviceFireEngine',
  components: {
    iotTable,
    deviceElestricCard,
    saveDevice
  },
  mounted() {
    this.queryDeviceslist()
    this.queryDevices()
    this.getreportform()
    this.startTime = new Date(new Date().toLocaleDateString()).getTime() / 1000

  },
  data() {
    return {
      loading: false,
      status: 1,
      reporttype: 1,
      tableData: [],
      tableData1: [],
      tableData2: [],
      devices: [],
      startTime: '',
      endTime: '',
      pagination: {
        page: 1,
        per_page: 10,
        total: 0
      },
      pagination1: {
        page: 1,
        per_page: 10,
        total: 0
      },
      pagination2: {
        page: 1,
        per_page: 10,
        total: 0
      },
      tableHeader: [
        { prop: 'province', label: '安装位置', type: 'slot' },
        { prop: 'electrovalency', label: '预设电价', type: 'slot' },
        { prop: 'activepower_totle', label: '有功功率', type: 'slot' },
        { prop: 'energy_total', label: '累计电能', type: 'slot' },
        { prop: 'powerfactor_totle', label: '功率因数' },
        { prop: 'mac', label: 'MAC' },
        { prop: 'create_time', label: '监测日期' }
      ],
      tableHeader1: [
        { prop: 'room', label: '使用部门', type: 'slot' },
        { prop: 'mac', label: '表计编号', type: 'slot' },
        { prop: 'energy_data_active', label: '有功电能', type: 'slot' },
        { prop: 'energy_data_reactive', label: '无功电能', type: 'slot' },
        { prop: 'max_electric_a', label: '相电流A最高', type: 'slot' },
        { prop: 'min_electric_a', label: '相电流A最低', type: 'slot' },
        { prop: 'average_electric_a', label: '相电流A平均', type: 'slot' },
        { prop: 'max_electric_b', label: '相电流B最高', type: 'slot' },
        { prop: 'min_electric_b', label: '相电流B最低', type: 'slot' },
        { prop: 'average_electric_b', label: '相电流B平均', type: 'slot' },
        { prop: 'max_electric_c', label: '相电流C最高', type: 'slot' },
        { prop: 'min_electric_c', label: '相电流C最低', type: 'slot' },
        { prop: 'average_electric_c', label: '相电流C平均', type: 'slot' },
        { prop: 'first_time', label: '起始时间', type: 'slot' },
        { prop: 'last_time', label: '终止时间', type: 'slot' }
      ],
      tableHeader2: [
        { prop: 'room', label: '使用部门', type: 'slot' },
        { prop: 'mac', label: '表计编号' },
        { prop: 'energy_data', label: '电量值', type: 'slot' },
        { prop: 'first_time', label: '起始时间', type: 'slot' },
        { prop: 'last_time', label: '终止时间', type: 'slot' }
      ]
    }
  },
  computed: {
    activeSite() {
      return this.$store.state.place.checkItem
    }
  },
  methods: {
    setStatus(i) {
      this.status = i
    },
    setreporttype(i) {
      this.reporttype = i
    },
    async queryDeviceslist() {
      this.loading = true
      const params = {
        page: this.pagination.page,
        per_page: this.pagination.per_page
      }
      try {
        const { data } = await placeelectricitylist(this.activeSite.place.id, params)
        this.tableData = data.data
        this.pagination = {
          total: data.page_info.total,
          page: data.page_info.page,
          per_page: data.page_info.per_page
        }
      } catch (e) {
        msgUtil.error('加载设备失败')

      }
      this.loading = false
    },
    async queryDevices() {
      this.loading = true
      const params = {
        page: this.pagination.page,
        per_page: this.pagination.per_page
      }
      try {
        const { data } = await placeelectricity(this.activeSite.place.id, params)
        this.devices = data.data
        console.log(this.devices)
        this.pagination = {
          total: data.page_info.total,
          page: data.page_info.page,
          per_page: data.page_info.per_page
        }
      } catch (e) {
        msgUtil.error('加载设备失败')

      }
      this.loading = false
    },
    async getreportform() {
      this.loading = true
      const data = new Date(); // 本月
      data.setDate(1);
      data.setHours(0);
      data.setSeconds(0);
      data.setMinutes(0);
      const startTime = this.startTime || parseInt(data.getTime() / 1000);
      if (this.reporttype == 1) {
        const params = {
          page: this.pagination2.page,
          per_page: this.pagination2.per_page,
          pid: this.activeSite.place.id,
          start_time: startTime
        }
        try {
          const { data } = await getelectricityreportplace(params)
          this.tableData1 = data
          this.pagination1 = {
            total: data.page_info.total,
            page: data.page_info.page,
            per_page: data.page_info.per_page
          }
        } catch (e) {
          msgUtil.error('加载设备失败')
        }
      } else if (this.reporttype == 2) {
        const params = {
          page: this.pagination2.page,
          per_page: this.pagination2.per_page,
          start_time: startTime,
          pid: this.activeSite.place.id
        }
        try {
          const { data } = await getelectricityreportplace(params)
          this.tableData2 = data
          this.pagination2 = {
            total: data.page_info.total,
            page: data.page_info.page,
            per_page: data.page_info.per_page
          }
        } catch (e) {
          msgUtil.error('加载设备失败')
        }
      }
      this.loading = false
    },
    lookday() {
      this.startTime = new Date(new Date().toLocaleDateString()).getTime() / 1000
      // this.endTime = ''
      this.getreportform()
    },
    lookmonth() {
      // 获取时间戳 (本月第一天00.00.00  本月最后一天23.59.59)
      var data = new Date(); // 本月
      data.setDate(1);
      data.setHours(0);
      data.setSeconds(0);
      data.setMinutes(0);
      this.startTime = parseInt(data.getTime() / 1000);
      // this.endTime = ''
      this.getreportform()
    },
    lookyear() {
      var data = new Date(); // 本月
      data.setDate(1);
      data.setMonth(0);
      data.setHours(0);
      data.setSeconds(0);
      data.setMinutes(0);
      this.startTime = parseInt(data.getTime() / 1000);
      // this.endTime = ''
      this.getreportform()
    },
    showAdd() {
      this.$refs.saveDevice.show()
    }
  }
}
</script>

