<template>
  <div>
    <div class="devicetype">
      <p class="t">{{titleName}} <span @click="handleBack">返回</span></p>
    </div>
    <div v-if="deviceId == 2"><device-smoke></device-smoke></div>
    <div v-else-if="deviceId == 5 || deviceId == 21 || deviceId == 22 || deviceId == 23 || deviceId == 7 || deviceId == 26 || deviceId == 15"><device-gas></device-gas></div>
    <div v-else-if="deviceId == 24"><device-fire-engine></device-fire-engine></div>
    <div v-else-if="deviceId == 'video'"><device-video></device-video></div>
    <div v-else-if="deviceId == 'patrol'"><device-patrol></device-patrol></div>
    <div v-else-if="deviceId == 11"><device-elestric></device-elestric></div>
  </div>
</template>

<script>
import deviceSmoke from './components/deviceSmoke'
import deviceGas from './components/deviceGas'
import deviceFireEngine from './components/deviceFireEngine'
import deviceVideo from './components/deviceVideo'
import devicePatrol from './components/devicePatrol'
import deviceElestric from './components/deviceElestric'

export default {
  name: 'Devicetype',
  components: {
    deviceSmoke,
    deviceGas,
    deviceFireEngine,
    deviceVideo,
    devicePatrol,
    deviceElestric
  },
  data() {
    return {

    }
  },
  computed: {
    titleName() {
      return this.$route.query.name || '设备'
    },
    deviceId() {
      return this.$route.params.type || '2'
    }
  },
  methods: {
    handleBack() {
      this.$router.back()
    }
  }
}
</script>

