<template>
  <div class="deviceFireEngineCard">
      <div class="second-title"><span @click="handleDet">{{ffDevice.mac}}</span> <span class="iconfont iconguanlian" @click="del($event, ffDevice.mac)"></span></div>
      <div class="desc-info">
        <p>
          <span class="iconfont iconditu"></span>
          <span class="val">
            <i>{{ffDevice.brand_name}}</i>
            |
            <i>{{ffDevice.model_name}}</i>
          </span>
        </p>
        <p>
          <span class="iconfont iconditu"></span>
          <span class="val">
            <i>{{ffDevice.address}}</i>
          </span>
        </p>
        <p>
          <span class="iconfont iconditu"></span>
          <span class="val">
            <i>{{ffDevice.user_name}}</i> | <i>{{ffDevice.user_account}}</i><i class="text-green">（共{{ffDevice.supervisor_count}}人）</i>
          </span>
        </p>
      </div>
      <div class="event-info">
        <p class="t" @click.stop="handleShow">事件信息</p>
        <div class="con" v-show="isActive">
        </div>
      </div>
    </div>
</template>

<script>
import { queryFFAlarms, unbindDevice } from '@/api/device'
import msgUtil from '@/utils/msgUtil'
import dateUtil from '@/utils/dateUtil'

export default {
  name: 'DeviceFireEngineCard',
  created() {
    document.addEventListener('click', this.handleHide);
  },
  props: {
    ffDevice: {
      required: true
    }
  },
  data() {
    return {
      isActive: false,
      alarms: []
    }
  },
  methods: {
    handleShow() {
      this.isActive = !this.isActive
      this.loadAlarms()
    },
    handleHide() {
      this.isActive = false
    },
    handleDet() {
      this.$router.push({
        path: `/equipment/devices-list/${this.$route.params.type}/${this.ffDevice.mac}`,
        query: {
          name: this.$route.query.name
        }
      })
    },
    async loadAlarms() {
      const params = {
        page: 1,
        per_page: 100,
        start: ~~(new Date().getTime() / 1000) - 60 * 60 * 24 * 365,
        end: ~~(new Date().getTime() / 1000),
        pid: this.$store.state.place.checkItem.place.id,
        mac: this.ffDevice.mac
      }
      try {
        const { data } = await queryFFAlarms(params)
        this.loadAlarmsStatus = true
        this.alarms = data.items.map(p => {
          p.alarm_time = dateUtil.format(new Date(p.alarm_time * 1000), 'YYYY-MM-dd HH:mm:ss')
          return p
        })
      } catch (error) {
        msgUtil.error('加载事件失败', msgUtil.findErrorMsg(error))
      }
    },
    del(e, mac) {
      e.stopPropagation()
      msgUtil.confirm('确定要删除消防主机联网？', '', () => {
        unbindDevice(mac).then(resp => {
          msgUtil.success('删除成功')
          this.$parent.queryDevices()
        }, error => {
          msgUtil.error('删除失败', msgUtil.findErrorMsg(error))
        })
      })
    }
  }
}
</script>

