 -->
<template>
  <div class="device-smoke">
    <div class="data">
      <iot-table
        :tableHeader="tableHeader"
        :tableData="tableData"
        :pagination="pagination"
        :loading="loading"
        @reloaddata="initData"
      >
        <div slot="col-install_address" slot-scope="scope"><p>{{scope.row.install_address}}-{{scope.row.building}}-{{scope.row.floor}}-{{scope.row.room}}</p></div>
        <div slot="col-job_state" slot-scope="scope"><p>{{scope.row.job_state === 0 ? '在线' : '离线'}}</p></div>
        <div slot="col-alarm_state" slot-scope="scope"><p>{{scope.row.alarm_state ? '已报警' : '正常'}}</p></div>
        <div slot="col-user_name" slot-scope="scope"><p>{{scope.row.user_name}} | {{scope.row.user_account}}</p></div>
        <div slot="col-mac" slot-scope="scope"><el-button @click="handleDetails(scope.row)" type="text" size="small">{{scope.row.mac}}</el-button></div>
        <div slot="col-confirm" slot-scope="scope">
          <el-button @click="handleMap(scope.row)" type="text" size="small"><span class="iconfont iconditu"></span></el-button>
          <el-button @click="handleDelete(scope.row)" type="text" size="small"><span class="iconfont iconshanchu"></span></el-button>
        </div>
      </iot-table>
    </div>
    <map-mark ref="mapMark" v-on:point-change="saveLocation"></map-mark>
  </div>
</template>

<script>
import { placeDevices, unbindDevicenew, moveDevice } from '@/api/device'
import msgUtil from '@/utils/msgUtil'
import mapMark from './dialogs/map_mark'
import iotTable from '@/components/iotTable'

export default {
  name: 'DeviceSmoke',
  components: {
    mapMark,
    iotTable
  },
  data() {
    return {
      loading: true,
      queryFilter: {
        mac: '',
        building: '',
        floor: '',
        room: '',
        wise_type: 'ALL'
      },
      tableData: [],
      pagination: {
        page: 1,
        per_page: 10,
        total: 0
      },
      tableHeader: [
        { prop: 'install_address', label: '安装位置', width: 300, type: 'slot' },
        { prop: 'last_time', label: '最后在线时间' },
        { prop: 'job_state', label: '设备状态', type: 'slot' },
        { prop: 'alarm_state', label: '报警状态', type: 'slot' },
        { prop: 'user_name', label: '监管人及电话', width: 300, type: 'slot' },
        { prop: 'mac', label: '设备mac', type: 'slot' },
        { prop: 'confirm', label: '操作', type: 'slot' }
      ]
    }
  },
  computed: {
    activeSite() {
      return this.$store.state.place.checkItem
    }
  },
  mounted() {
    if (this.activeSite.place.id) {
      this.initData()
    }
  },
  methods: {
    async initData() {
      this.loading = true
      const params = {
        page: this.pagination.page,
        per_page: this.pagination.per_page,
        type: this.$route.params.type, //
        mac: this.queryFilter.mac ? this.queryFilter.mac : undefined,
        building: this.queryFilter.building ? this.queryFilter.building : undefined,
        floor: this.queryFilter.floor ? this.queryFilter.floor : undefined,
        room: this.queryFilter.room ? this.queryFilter.room : undefined,
        wise_type: this.queryFilter.wise_type
      }
      try {
        const { data } = await placeDevices(this.$store.state.place.checkItem.place.id || this.$route.query.placeid, params)
        if (data.page_info.total > 0) {
          data.items.forEach(i => {
            // console.log(i)
            i.install_address = this.activeSite.place.name
          })
          this.noDevices = false
        } else {
          this.noDevices = true
        }
        this.tableData = data.items
        this.pagination = {
          total: data.page_info.total,
          page: data.page_info.page,
          per_page: data.page_info.per_page
        }
      } catch (error) {
        msgUtil.error('加载项目数据失败', msgUtil.findErrorMsg(error))
      }
      this.loading = false
    },
    handleMap(row) {
      this.$refs.mapMark.show(row.lng, row.lat, row)
    },
    handleDelete(row) {
      msgUtil.confirmdevice('确定要解绑？', row.mac, '解绑后将无法接收设备消息!', (verifycode) => {
        unbindDevicenew(row.mac, verifycode).then(resp => {
          if (resp.status === 200) {
            msgUtil.info('解绑成功')
            this.initData()
          } else {
            msgUtil.error('解绑失败', resp.msg)
          }
        }, error => {
          msgUtil.error('解绑失败', msgUtil.findErrorMsg(error))
        })
      })
    },
    handleDetails(row) {
      this.$router.push({
        path: `/equipment/devices-list/${this.$route.params.type}/${row.mac}`,
        query: {
          name: this.$route.query.name
        }
      })
    },

    saveLocation(e) {
      moveDevice({
        mac: e.obj.mac,
        lat: e.lat.toString(),
        lng: e.lng.toString()
      }).then(resp => {
        this.initData()
        msgUtil.success('更新成功', '已更新设备位置')
      }, error => {
        msgUtil.error('更新失败', msgUtil.findErrorMsg(error))
      })
    }
  }
}
</script>

<style lang="less" scoped>
  .device-smoke{

  }
</style>
