          </li>
          <!-- <li class="col-lg-6" :class="{'warning': $v.info.electrovalency.$dirty && $v.info.electrovalency.$invalid}"> -->
          <li class="col-lg-6">
            <p class="input-title">
              电价
              <small class="fr">*请输入电价</small>
            </p>
            <input type="number" min="0" placeholder="请输入电价" v-model="info.electrovalency"/>
            <!-- <input type="number" min="0" placeholder="请输入电价" v-model="info.electrovalency" @input="$v.info.electrovalency.$touch"/> -->
          </li>
          <li class="col-lg-6">
          <!-- <li class="col-lg-6" :class="{'warning': $v.info.current_transformer_parameters.$dirty && $v.info.current_transformer_parameters.$invalid}"> -->
            <p class="input-title">
              互感器电流参数
              <small class="fr">*请输入设备互感器电流参数</small>
            </p>
            <!-- <input type="text" placeholder="请输入设备设备MAC" v-model="info.mac" @input="$v.info.mac.$touch()"/> -->

            <el-select v-model="info.current_transformer_parameters" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.value"
